<template>
	<view  style="background-color: #f1f1f1;">
		<cu-custom bgColor="bg-gradual-blue">
			<block slot="content">个人信息</block>
		</cu-custom>

		<view class="cu-card article no-card">
			<view class="cu-item shadow padding-top">
				<view class="content">
					<view class="desc text-center">
						<view class="flex align-center">
							<view class="">
								<view class="cu-avatar xl round margin-left"
									:style="{backgroundImage:'url(' + avatarUrl + ')'}"></view>
								<!-- <view class="cu-avatar xl round margin-left" style="background-image:url(https://ossweb-img.qq.com/images/lol/web201310/skin/big99008.jpg);"></view> -->
							</view>
							<view class="usertitle">{{userinfoObj.username}}</view>
						</view>
					</view>
				</view>
			</view>
		</view>

		<view class="cu-list menu margin-top-sm">
			<view class="cu-item shadow">
				<view class="content">
					<text class="cuIcon-friendfamous text-blue"></text>
					<text>账号：{{userinfoObj.account}}</text>
				</view>
			</view>
			<view class="cu-item shadow">
				<view class="content">
					<text class="cuIcon-mail text-blue"></text>
					<text>邮箱：{{userinfoObj.email}}</text>
				</view>
			</view>
			<view class="cu-item shadow">
				<view class="content">
					<text class="cuIcon-mobile text-blue"></text>
					<text>电话：{{userinfoObj.phone}}</text>
				</view>
			</view>
			<view class="cu-item arrow shadow" @click="tosee">
				<view class="content">
					<text class="cuIcon-edit text-blue"></text>
					<text>我的预约</text>
				</view>
			</view>
			
		</view>
		
		

		

		<view class="cu-card article no-card padding-top-xs">
			<view class="cu-item shadow padding-top">
				<view class="content">
					<view class="desc text-center text-grey" @click="outlogin">
						退出登录
					</view>
				</view>
			</view>
		</view>

	</view>
</template>

<script>
	export default {
		created() {
			try{
				this.userinfoObj = JSON.parse(uni.getStorageSync('userinfo'))
			}catch(e){
				this.userinfoObj = ""
			}
		},
		onTabItemTap(e) {
			// tab 点击时执行，此处直接接收单击事件
			this.userinfoObj = JSON.parse(uni.getStorageSync('userinfo'))
		},
		onLoad() {
			
		},
		data() {
			return {
				isshow: true,
				name: '点击登录',
				namenew: '',
				usering: true,
				userdata: {},
				flagbj:false,
				userinfoObj:{},
				avatarUrl:'/static/01.png'
			}
		},
		onShow() {
			this.ifUser()
		},
		methods: {
			//跳转至商品维护页面
			toeditfood(){
				uni.navigateTo({
					url: '../setfood/setfood'
				});
			},
			outlogin() {
				uni.showModal({
					content: '确定要退出吗？',
					cancelText: '取消',
					confirmText: '确定',
					success:async res => {
						if (res.confirm) {
							uni.removeStorageSync('userinfo')
							uni.navigateTo({
								url: '/pages/login/login'
							});
						}
					}
				})
			},
			coupon() {
				let user = uni.getStorageSync('wxuser')
				if (!user) {
					uni.showToast({
						title: '请先登录',
						icon: 'none',
						duration: 2000
					});
				} else {
					uni.navigateTo({
						url: 'coupon'
					})
				}
			},
			tologin(){
				uni.showToast({
					title: '请先登录',
					icon: 'none',
					duration: 2000
				});
				setTimeout(()=>{
					uni.navigateTo({
						url: '/pages/login/login'
					});
				},2000)
				
			},
			tosee(){
				if (!this.userinfoObj) {
					this.tologin()
					return false
				}
				uni.navigateTo({
				     url: '../myorder/myorder'
				    });
			},
			address() {
				let user = uni.getStorageSync('wxuser')
				if (!user) {
					uni.showToast({
						title: '请先登录',
						icon: 'none',
						duration: 2000
					});
				} else {
					uni.navigateTo({
						url: 'address'
					})
				}

			},
			ifUser() {
				let user = uni.getStorageSync('wxuser')
				console.log(user)
				if (!user) {
					this.usering = true
					console.log("没cookiet")
					let userdata = {
						avatarUrl: "'../../static/tab/tab-g.png'",
						namenew: "namenew"
					}
					this.userdata = userdata


				} else {
					this.usering = false
					this.userdata = user
					console.log("有cookiet")
				}
			},
			loginTest() { // 获取用户信息
				var that = this;
				var _this = this;
				uni.showLoading({
					title: '请稍等...',
					mask: true
				});
				uni.getUserProfile({
					lang: 'zh_CN',
					desc: '登录',
					success: (res) => {
						that.flagbj = true
						console.log(res);
						//这一步已经获取到用户信息
						that.avatarUrl = res.userInfo.avatarUrl;
						that.namenew = res.userInfo.nickName;
						if(res.userInfo.nickName.indexOf('clam')){
							that.flagbj = true
						}else{
							that.flagbj = false
						}
						that.userdata = {
							avatarUrl: that.avatarUrl,
							namenew: that.namenew
						}

						uni.setStorage({
							key: 'wxuser',
							data: that.userdata
						});
						that.ifUser()

						//延时两秒后跳转
						//用户权限判断
						uni.hideLoading();
					},
					fail: (res) => {
						console.log(res)
					}
				});
			},

		}
	}
</script>

<style>
	.usertitle {
		margin-left: 10px;
		font-size: 1.2em;
	}
</style>
